import React from 'react';

let CompetitionSummary = ({ teams }) => {

   return (
      <div>
         <div>All teams in competition (click team to see summary):</div>
         {teams.map((t) => {
            return (<span key={t} onClick={()=>handleClick(t)}>{t}, </span>);
         })}

         <iframe className="iframe" title="Hello" src="" width="540" height="450"></iframe>

      </div>
      
   );
}

function handleClick(t) {
   document.getElementsByClassName("iframe")[0].src = `/tba-at-a-glance?team=${t}`
}

export default CompetitionSummary;



